html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-icon-1 {
  width: 0.85em;
  height: 1em;
  fill: initial;
}

.icon-icon-2 {
  width: 0.88em;
  height: 1em;
  fill: initial;
}

.icon-icon-3 {
  width: 0.84em;
  height: 1em;
  fill: initial;
}

.icon-icon-4 {
  width: 1.18em;
  height: 1em;
  fill: initial;
}

.icon-icon-5 {
  width: 0.78em;
  height: 1em;
  fill: initial;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@font-face {
  font-family: helvet;
  src: url("../fonts/HelveticaNeue.woff2") format("woff2"), url("../fonts/HelveticaNeue.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: bebas;
  src: url("../fonts/BebasNeueRegular.woff2") format("woff2"), url("../fonts/BebasNeueRegular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: url("../img/bg-body.png") repeat;
  font-family: helvet, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  font-weight: 500;
  color: #414141;
}

a {
  text-decoration: none;
}

.main-i {
  display: flex;
}

@media only screen and (max-width: 600px) {
  .main-i {
    flex-direction: column;
  }
}

.main-content {
  width: 670px;
}

@media only screen and (max-width: 960px) {
  .main-content {
    width: 71%;
  }
}

@media only screen and (max-width: 600px) {
  .main-content {
    width: 100%;
  }
}

.aside {
  width: 220px;
  margin-left: 50px;
}

@media only screen and (max-width: 960px) {
  .aside {
    width: 24%;
    margin-left: 5%;
  }
}

@media only screen and (max-width: 600px) {
  .aside {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-left: 0;
  }
}

.container {
  width: 960px;
  padding: 0 10px;
  margin: 0 auto;
}

@media only screen and (max-width: 960px) {
  .container {
    width: 100%;
  }
}

.btn-nav {
  position: absolute;
  top: 25px;
  right: 15px;
  margin-left: auto;
  display: none;
}

@media only screen and (max-width: 768px) {
  .btn-nav {
    display: block;
    width: 25px;
    height: 25px;
    background-image: url("../img/burger.svg");
    background-color: transparent;
    fill: black;
  }
}

.btn-nav.active {
  background-image: url("../img/close.svg");
}

.header__layout {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  padding: 20px 0;
}

@media only screen and (max-width: 768px) {
  .header__layout {
    flex-direction: column;
  }
}

.main-nav {
  position: relative;
  margin-left: auto;
  margin-top: 10px;
}

@media only screen and (max-width: 768px) {
  .main-nav {
    display: none;
  }
}

.main-nav.active {
  display: block;
  width: 80%;
  text-align: center;
  margin-right: auto;
  margin-top: 20px;
}

.main-nav__list {
  display: flex;
  font-family: bebas , sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

@media only screen and (max-width: 768px) {
  .main-nav__list {
    flex-direction: column;
  }
}

.main-nav__item {
  margin-left: 30px;
}

@media only screen and (max-width: 768px) {
  .main-nav__item {
    width: 100%;
    padding: 10px;
    margin-left: 0;
    border-bottom: 1px solid #e8603c;
  }
}

.main-nav__link {
  font-size: 19px;
  color: #b9b9b9;
}

.main-nav__link:hover {
  color: #f3b29d;
}

.main-nav__link.is-active {
  color: #e8603c;
}

.main-header {
  display: flex;
  margin-bottom: 24px;
}

.main-header__title {
  width: 100%;
  padding: 15px 20px;
  background-color: #e8603c;
  font-family: bebas , sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: white;
  font-size: 30px;
  line-height: 1;
}

.main-header__decor {
  flex-grow: 1;
  min-height: 61px;
  background-color: rgba(232, 96, 60, 0.5);
}

.post {
  position: relative;
  width: 100%;
  padding-bottom: 45px;
  margin-bottom: 55px;
  background: url("../img/bg-grey.png") repeat-x bottom;
}

.post__icon {
  position: absolute;
  left: -85px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75px;
  height: 75px;
  font-size: 28px;
  background-color: #e8603c;
}

.post__icon svg {
  fill: #fff;
}

.post__media {
  position: relative;
  width: 100%;
  margin-bottom: 16px;
}

.post__media img {
  width: 100%;
  height: 300px;
  height: calc(143px + (300 - 143) * ((100vw - 320px) / (960 - 320)));
  object-fit: cover;
}

@media (max-width: 320px) {
  .post__media img {
    height: 143px;
  }
}

@media (min-width: 960px) {
  .post__media img {
    height: 300px;
  }
}

.post__media .slick-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;
  width: 36px;
  height: 52px;
  font-size: 0;
}

.post__media .slick-prev {
  left: 20px;
  background: url("../img/prev.png") no-repeat 50% 50%;
}

.post__media .slick-next {
  right: 20px;
  background: url("../img/next.png") no-repeat 50% 50%;
}

.post__video {
  padding: 56.25% 0 0 0;
}

.post__video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.post__content {
  display: flex;
  padding-right: 10px;
}

.post__title {
  margin-bottom: 16px;
  font-family: bebas , sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 22px;
  line-height: 1;
  letter-spacing: 0.8px;
  color: #e8603c;
  text-transform: uppercase;
}

.post__text p {
  margin-top: 20px;
  margin-bottom: 17px;
}

.post__descr .post__title {
  margin-bottom: 0;
}

.post__link-text {
  color: #a0a0a0;
}

.post__quotes {
  padding: 17px;
  padding-left: 75px;
  margin-bottom: 15px;
  font-size: 14px;
  background: whitesmoke url("../img/bg-quotes.png") no-repeat 35px 15px;
}

.post-data {
  width: 440px;
  margin-right: 20px;
  font-size: 12px;
  line-height: 1.66;
}

.post-data__item {
  margin-bottom: 6px;
}

.post-data__title {
  font-family: bebas , sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 15px;
  color: #e8603c;
  text-transform: uppercase;
}

.post-data__disc {
  color: #bababa;
}

.post-data__disc a {
  color: #bababa;
}

.pagination {
  margin-top: -15px;
  margin-left: 5px;
  margin-bottom: 375px;
}

@media only screen and (max-width: 600px) {
  .pagination {
    margin-bottom: 60px;
  }
}

.pagination__list {
  display: flex;
}

.pagination__item {
  margin-right: 5px;
}

.pagination__page {
  display: block;
  text-align: center;
  width: 25px;
  padding: 7px 0;
  font-size: 14px;
  line-height: 1;
  text-decoration: none;
  color: white;
  background-color: #f3b29d;
}

.pagination__page--active {
  background-color: #e8603c;
}

.aside__head {
  display: flex;
}

@media only screen and (max-width: 768px) {
  .aside__head {
    font-size: 12px;
  }
}

.aside__head-line {
  margin-left: 15px;
  flex-grow: 1;
  background: url("../img/bg-grey.png") repeat-x;
  background-position: center;
}

.aside__title {
  margin-bottom: 12px;
  font-family: bebas , sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: #e8603c;
  font-size: 16px;
  letter-spacing: 1px;
}

.aside__item {
  width: 100%;
  margin-bottom: 30px;
}

.about-jetro__content {
  line-height: 1.2;
}

.categories {
