}

.gallery {
  width: 100%;
}

.gallery__content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.gallery__item {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  border: 1px solid #e8603c;
}

.gallery__item img {
  width: 100%;
  height: 100%;
}
