.main-i {
  display: flex;
  @include r(600) {
    flex-direction: column; } }

.main-content {
  width: 670px;
  @include r(960) {
    width: 71%; }
  @include r(600) {
    width: 100%; } }

.aside {
  width: 220px;
  margin-left: 50px;
  @include r(960) {
    width: 24%;
    margin-left: 5%; }
  @include r(600) {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-left: 0; } }
